.nav {
  &__link {
    position: relative;
    color: inherit;
    text-decoration: none;

    font-size: 22px;
    font-weight: 700;
    line-height: 22px;

    &::after {
      content: "";
      position: absolute;
      bottom: -4px;
      display: block;
      height: 1px;
      width: 100%;
      background: $main-text-color;
      transition: transform $time-anination;
      transform: scale(0);
      transform-origin: left;
    }

    &:hover::after {
      transform: scale(1);
    }
  }

  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 32px;

  }
}
